/* spartan-100 - latin-ext */
@font-face {
  font-family: 'Spartan';
  font-style: normal;
  font-weight: 100;
  src: url('../fonts/spartan-v10-latin-ext-100.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../fonts/spartan-v10-latin-ext-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/spartan-v10-latin-ext-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/spartan-v10-latin-ext-100.woff') format('woff'), /* Modern Browsers */
       url('../fonts/spartan-v10-latin-ext-100.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/spartan-v10-latin-ext-100.svg#Spartan') format('svg'); /* Legacy iOS */
}
